.iconsdisigne:hover{
   
       background: rgb(254, 252, 252); 
      border-radius: 50px;
      height: 150px;
      transition: var(--transition) ;
      color: black;
      transform: translateX(-0.5rem);
      
  }
  
  .backgroundbody{
    height: 1300px;
    background:white;
  }
  
   .techcss{
    padding-top: 50px;
    margin-bottom: 50px;
  
   }
   .headcss{
    margin-top: 50px;
    margin-bottom: 30px;
   }
   .iconsdisigne{
    width: 60%;
    margin: auto;
    font-size: 70px;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-left: 100px;
     box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;

     background: rgb(139, 63, 210,0.5);
     color: white;
      border-radius: 50px;
      height: 150px;
      transition: var(--transition) ;
  }
  .iconsbrand{
    display:grid ;
    grid-template-columns: repeat(4 , 1fr);
    gap:1rem;
   
  }

@media screen and (max-width:1024px) {
  .iconsbrand{
      grid-template-columns:repeat(3,1fr);
      gap: 1rem;
      width: 90%;
     margin-right: 0px;
  }
  .backgroundbody{
    height: 1600px;
   
  }
 
  h6{
    font-size: 10px;
  }
  h5{
    font-size: 13px;
  }
 
}

@media screen and (max-width:480px) {
  .iconsbrand{
      grid-template-columns: repeat(2,1fr);
      gap: 1rem;
      width: 50%;
      margin-left: 40px;
      text-align: center;
      justify-content: center;
      align-items: center;
     
  }
  h3{
    font-size: 20px;
    text-align: center;
  }
  .backgroundbody{
    height: auto;
    margin-bottom: 10px;
  }

  .iconsdisigne{
    width: 80%;
  }
  h6{
    font-size: 10px;
  }
  h5{
    font-size: 15px;
  }
}


